*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #d5d4d4;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.app{
 
  
  transition: 0.4s ease-out;
  object-fit: contain;
  background-image: url("https://cdn.pixabay.com/photo/2018/08/23/07/35/thunderstorm-3625405_1280.jpg");
  background-size: cover;
  background-position: center center;

}

main{
  min-height: 100vh;
  
  background-image:linear-gradient(to bottom,rgba(0,0,0,0.2),rgba(0,0,0,0.75));
}
.app .warm{
  background-image: url("https://images.unsplash.com/photo-1631242389884-9fa33819be7f?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxzZWFyY2h8MTJ8fHdlYXRoZXIlMjB3YXJtfGVufDB8fDB8fHww&auto=format&fit=crop&w=500&q=60");
}

.search-bar{
  width: 50%;
  height: 40px;
  margin:0 20px ;
  background-color: rgba(255,255,255,0.2);
  border: none;
  outline: none;
  font-size: 20px;
  border-radius: 0px 0px 16px 16px;
  box-shadow: 0px 5px rgba(0,0,0,0.5);
  
  
}
::placeholder{
  color:black;
}
.search-box{
  display: flex;
  justify-content: center;
}
.search-box .search-bar:focus{
  background-color: rgba(255,255,255,0.9);
}
.location-box .location{
  font-size: 40px;
  font-weight: 600;
  color:#FFF;
  text-align: center;
  text-shadow: 3px 3px rgba(50,50,50,0.2);

}
.location-box .date{
  color:#FFF;
  font-size: 32px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow:4px 4px rgba(50,50,50,0.2)
}
.weather-box{
  text-align: center;
}
.weather-box .temp{
  position: relative;
  display: inline-block;
  margin:30px auto;
  background-color: rgba(255,255,255,0.2);
  border-radius: 16px;
  padding: 15px 25px;
  color:#FFF;
  font-size: 102px;
  text-shadow: 3px 6px rgba(50,50,60,0.2);
  text-align: center;
  font-weight: 700;
}
.weather-box .weather{
  color:#FFF;
  font-size: 50px;
  font-weight: 700;
  text-shadow: 3px 6px rgba(50,50,50,0.7);
}